.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%;
}

.navlist {
  display: flex;
  background-color: #6d8e46;
  height: 10px;
  align-items: center;
}
.navlist li {
  flex: 1;
  display: block;
  list-style-type: none;
}
.navlist li:hover {
  background-color: #afd683;
  display: block;
}

.active {
  background-color: #9ccc65;
}
